// app/layout.tsx
import "./globals.css";
import Header from "./components/Header";
import { LanguageProvider } from "@/context/LanguageContext";
import { ThemeProvider } from "@/context/ThemeContext";
import { Poppins, Playwrite_NZ_Basic, Playwrite_IN_Guides } from "next/font/google";

const poppins = Poppins({
  subsets: ["latin"],
  weight: ["300", "400", "500", "600", "700", "800"],
  variable: "--font-poppins",
  display: "swap",
});

const playwriteNZ = Playwrite_NZ_Basic({
  subsets: ["latin"],
  weight: ["400"], // this font only has regular
  variable: "--font-playwrite-nz",
  display: "swap",
});

const playwriteIndia = Playwrite_IN_Guides({
  subsets: ["latin"],
  weight: ["400"], // this font only has regular
  variable: "--font-playwrite-india",
  display: "swap",
});

export default function RootLayout({
  children,
}: {
  children: React.ReactNode;
}) {
  return (
    <html lang="en" suppressHydrationWarning>
      <body
        className={`
          ${poppins.variable} 
          ${playwriteNZ.variable} 
          ${playwriteIndia.variable} 
          font-sans antialiased min-h-screen
        `}
        suppressHydrationWarning
      >
        <ThemeProvider>
          <LanguageProvider>
            <Header />
            <main>{children}</main>
          </LanguageProvider>
        </ThemeProvider>
      </body>
    </html>
  );
}